/* globals console, window, document */

const additionalMergeFieldsConfig = {
	previewModes: [ '$labels' ]
};

window.ClassicEditor
	.create( document.querySelector( '#snippet-merge-fields-labels' ), {
		mergeFields: { ...window.ClassicEditor.defaultConfig.mergeFields, ...additionalMergeFieldsConfig },
		toolbar: {
			items: [ 'insertMergeField' ].concat( window.ClassicEditor.defaultConfig.toolbar.items ),
			shouldNotGroupWhenFull: true
		},
		initialData: '<p style="text-align: center;">Dear {{guestTitle}} {{guestName}}</p>' +
			'<p>This email confirms your reservation at Serenity Springs Resort.' +
			'We\'re delighted to welcome you on <strong>{{guestArrivalDate}}</strong>!</p>' +
			'<figure class="image" width="600" height="400">' +
			'<img src="{{hotelRoomPhoto}}" />' +
			'<figcaption>Quick look into your {{roomType}} experience</figcaption>' +
			'</figure>' +
			'{{additionalValueProposition}}' +
			'<p><em>Sincerely</em><br /><em>The Team at Serenity Springs Resort</em></p>'
	} )
	.then( editor => {
		window.editor = editor;

		// Prevent showing a warning notification when user is pasting a content from MS Word or Google Docs.
		window.preventPasteFromOfficeNotification = true;

		window.attachTourBalloon( {
			target: window.findToolbarItem(
				editor.ui.view.toolbar, item => item.buttonView && item.buttonView.label === 'Insert merge field'
			),
			text: 'Click to add a merge field.',
			editor,
			tippyOptions: {
				placement: 'bottom-start'
			}
		} );
	} )
	.catch( err => {
		console.error( err.stack );
	} );
